








































































header.focusHeader{
	display: block;
    z-index: 9999;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    height: 2.8125rem;
}
header .h_button{
	overflow: hidden;
	width: 100%;
	height: 2.8125rem;
	background-color: #0DC441;
}
header .h_button .h_buttonIn{
	width: 7.5rem;
	height: 1.5625rem;
	line-height: 1.5625rem;
	margin: 0.625rem auto;
}
header .h_button .h_buttonIn > div{
	width: 50%;
	font-size: 0.9rem;
	text-align: center;
	color: #A3E9B7;
	background-color: rgba(61,208,103,.8);
}
header .h_button .h_buttonIn > div:nth-of-type(1){
	border-radius: 15px 0 0 15px;
}
header .h_button .h_buttonIn > div:nth-of-type(2){
	border-radius: 0 15px 15px 0;
}
header .h_button .h_buttonIn .active{
	color: white;
	background-color: rgba(100,230,135,.8);
}
.main.main_sp{
	overflow: hidden;
	position: absolute;
	top: 2.8125rem;
	left: 0;
	bottom: 2.8125rem;
	width: 100%;
}
.main .f_box{
	width: 100%;
}
.main .f_box .f_boxTtile{
	position:relative;
	height: 3.125rem;
	padding: 0.75rem;
	background-color: white;
}
.main .f_box .f_boxTitleImg{
	width: 3.125rem;
	height: 3.125rem;
}
.main .f_box .f_boxTitleImg img{
	width: 100%;
	border: 1px solid #B4B4B4;
	border-radius: 100%;
}
.main .f_box .f_boxTtile dl{
	margin-left: 0.625rem;
}
.main .f_box .f_boxTtile dl dt{
	line-height: 1.5rem;
	font-size: 1rem;
	color: #000000;
}
.main .f_box .f_boxTtile dl dd{
	line-height: 1.5rem;
	font-size: 0.9rem;
	color: #999999;
}
.main .f_box .f_boxTtile .f_count{
	position: absolute;
	right: 0.75rem;
	top: 0.75rem;
	color: #CCCCCC;
}
.main .f_box .f_boxTtile .f_count a i{
	color: #CCCCCC;
}
.main .f_box .f_boxContent{
	width: 100%;
}
.main .f_box .f_boxContent .f_contentImg img{
	width: 100%;
}




