	display: block;
    z-index: 9999;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    height: 5.0625rem;
}
header .h_search{
	overflow: hidden;
	width: 96.85%;
	height: 2.8125rem;
	line-height: 2.8125rem;
	margin: 0 auto;
}
header .h_search input{
	width: 80%;
	height: 1.4rem;
	margin: 0.65625rem 0;
	padding-left: 3%;
	border: 1px solid #AAAAAA;
	border-radius: 5px;
}
header .h_search button{
	width: 14.838%;
	height: 1.5rem;
	line-height: 1.5rem;
	margin: 0.65625rem 0;
	background-color: #0DC441;
	border: 0;
	border-radius: 5px;
	color: white;
	letter-spacing: 1px;
}
.main{
	overflow: hidden;
	position: absolute;
	top: 6.0625rem;
	left: 0;
	bottom: 2.8125rem;
	width: 100%;
}
.main .f_box{
	width: 100%;
	border-bottom: 1px solid #E5E5E5;
}
.main .f_box .f_boxTtile{
	position:relative;
	height: 3.125rem;
	padding: 0.75rem;
	background-color: white;
}
.main .f_box .f_boxTitleImg{
	width: 3.125rem;
	height: 3.125rem;
}
.main .f_box .f_boxTitleImg img{
	width: 100%;
	border: 1px solid #B4B4B4;
	border-radius: 100%;
}
.main .f_box .f_boxTtile dl{
	margin-left: 0.625rem;
}
.main .f_box .f_boxTtile dl dt{
	line-height: 1.5rem;
	font-size: 1rem;
	color: #000000;
}
.main .f_box .f_boxTtile dl dd{
	line-height: 1.5rem;
	font-size: 0.9rem;
	color: #999999;
}
.main .f_box .f_boxTtile .f_count{
	position: absolute;
	right: 0.75rem;
	top: 0.75rem;
	color: #CCCCCC;
}
.main .f_box .f_boxTtile .f_count a i{
	color: #CCCCCC;
}




