













html {
  height: 100%;
}

body {
  height: 100%;
}

.loading{
  text-align: center;
  font-size: 16px;
  color: pink
}

.view {
  position: absolute;
  height: 100%;
  min-width: 100%;
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
}
.test-enter, .test-leave {
  opacity: 0;
  -webkit-transform: translate3d(10px, 10px, 0);
          transform: translate3d(10px, 10px, 0);
}
[v-clock]{
	display: none;
}



